.nvm-poap-widget {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  &__banner {
    justify-content: center;
    width: 100%;
    margin-top: 2%;
  }
  &__reedem-container {
    padding-top: 20px;
    width: 100%;
    font-size: 30px;
    flex-direction: column;
  }
  &__reedem, &__text {
    justify-content: center;
  }
  &__reedem {
    &--enable-button {
      cursor: pointer;
    }
    &--disable-button {
      background: gray !important;
      cursor: not-allowed;
    }
  }

  &__text {
    &--warning {
      color: #000 !important;
    }
  }
}